body {
	background-color: #272727;
}

.App {
	display: flex;
	flex-direction: column;
	align-items: center;
	position: relative;
}

#pieChart {
	position: relative;
}

#bubblePlot {
	position: relative;
}

.tooltip {
	position: absolute;
	padding: 5px;
	background-color: white;
	border-radius: 10px;
	display: flex;
	justify-content: center;
	align-items: center;
}

.axisWhite path,
.axisWhite line {
	stroke: white;
}

.mid {
	display: flex;
	flex-direction: row;
}
.chordLabelText {
	cursor: pointer;
}

.dashboardCard {
	background-color: #2c2c2c;
	border-radius: 10px;
	display: flex;
	flex-direction: column;
	align-items: center;
	padding: 0px 20px;
	-webkit-box-shadow: 10px 10px 5px -3px rgba(0, 0, 0, 0.73);
	-moz-box-shadow: 10px 10px 5px -3px rgba(0, 0, 0, 0.73);
	box-shadow: 10px 10px 5px -3px rgba(0, 0, 0, 0.73);
	margin: 20px;
}

.dashboardCardTitle {
	color: white;
	font-size: 20px;
	font-weight: bold;
	padding: 10px 0;
}

.header {
	width: 100%;
	background-color: #2c2c2c;
	display: flex;
	flex-direction: column;
	align-items: center;
	margin-bottom: 30px;
	-webkit-box-shadow: 10px 10px 5px -3px rgba(0, 0, 0, 0.73);
	-moz-box-shadow: 10px 10px 5px -3px rgba(0, 0, 0, 0.73);
	box-shadow: 10px 10px 5px -3px rgba(0, 0, 0, 0.73);
}

.charizard {
    position: absolute;
    bottom: 50px; 
    right: 0;
    z-index: -1;
}

.charizard img{
    width: 400px;
}

.pikachu{
    position: absolute;
    bottom: 50px;
    left: 0;
    z-index: -1;
}
